import Vue from "vue";
import Vuex from "vuex";
import Resoluciones from "@/data/resoluciones";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    year: null,
    title: ""
  },
  mutations: {
    SET_YEAR(state, { year }) {
      state.year = year;
    },
    SET_TITLE(state, title) {
      state.title = title;
    }
  },
  actions: {},
  getters: {
    getResolucionesAnio: state => {
      if (!state.year) return null;

      return Resoluciones.find(x => x.year === state.year);
    },
    getResolucionesMes: (state, getters) => semestre => {
      if (!getters.getResolucionesAnio) return null;

      const resolucionesSemestre =
        semestre === 1
          ? getters.getResolucionesAnio.cantPorMes.slice(0, 6)
          : getters.getResolucionesAnio.cantPorMes.slice(6);

      return resolucionesSemestre;
    },
    getTiposLabel: (state, getters) => {
      if (!getters.getResolucionesAnio) return null;
      return Object.keys(getters.getResolucionesAnio.tipos);
    },
    getTiposValues: (state, getters) => {
      if (!getters.getResolucionesAnio) return null;
      return Object.values(getters.getResolucionesAnio.tipos);
    }
  },
  modules: {}
});
